<template>
  <!-- 日志管理 -->
  <div class="container">
    <div class="header">
      <el-date-picker
        v-model="value2"
        type="daterange"
        align="right"
        unlink-panels
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        :picker-options="pickerOptions"
      >
      </el-date-picker>
      <el-button type="danger">批量删除</el-button>
    </div>
    <div class="con">
      <el-table
        :data="tableData"
        style="width: 100%"
        :row-class-name="tableRowClassName"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
          prop="newsTitle"
          label="消息标题"
          width="250"
        ></el-table-column>
        <el-table-column
          prop="newsCon"
          label="消息内容"
          width="460"
        ></el-table-column>
        <el-table-column
          prop="sender"
          label="发送者"
          width="180"
        ></el-table-column>
        <el-table-column
          prop="time"
          label="发送时间"
          width="200"
        ></el-table-column>
        <el-table-column prop="operat" label="操作" width="280">
          <template slot-scope="scope">
            <el-button size="mini" type="danger">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="buttonCon">
      <el-pagination background layout="prev, pager, next" :total="1000">
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  name: "user",
  data() {
    return {
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },
      tableData: [
        {
          newsTitle: "消息标题消息标题",
          newsCon: "消息内容消息内容消息内容消息内容消息内容消息内容消息内容消息内容消息内容消息内容消息内容消息内容消息内容消息内容消息内容消息内容消息内容消息内容消息内容消息内容消息内容",
          sender: "王小虎",
          time: "2016-05-04 02:02:02",
        },{
          newsTitle: "消息标题消息标题",
          newsCon: "消息内容消息内容消息内容消息内容消息内容消息内容消息内容",
          sender: "王小虎",
          time: "2016-05-04 02:02:02",
        },{
          newsTitle: "消息标题消息标题",
          newsCon: "消息内容消息内容消息内容消息内容消息内容消息内容消息内容",
          sender: "王小虎",
          time: "2016-05-04 02:02:02",
        },{
          newsTitle: "消息标题消息标题",
          newsCon: "消息内容消息内容消息内容消息内容消息内容消息内容消息内容",
          sender: "王小虎",
          time: "2016-05-04 02:02:02",
        },{
          newsTitle: "消息标题消息标题",
          newsCon: "消息内容消息内容消息内容消息内容消息内容消息内容消息内容",
          sender: "王小虎",
          time: "2016-05-04 02:02:02",
        },{
          newsTitle: "消息标题消息标题",
          newsCon: "消息内容消息内容消息内容消息内容消息内容消息内容消息内容",
          sender: "王小虎",
          time: "2016-05-04 02:02:02",
        }
      ],
    };
  },
  methods: {},
};
</script>
<style scoped>
.header .el-button {
  margin: 0 10px;
}
.el-input {
  width: 150px;
  margin: 10px 0;
}
.header {
  padding: 10px;
  background-color: #fff;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.buttonCon {
  margin-top: 10px;
  text-align: right;
}

.cell {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
</style>


